import { NavLink } from 'react-router-dom';
import classes from './navbar.module.css';

function Navbar() {
    return(
      <nav className={classes.nav}>
      <div className={classes.links}>
        <div className={classes.item}><NavLink to="/profile" className={navData => navData.isActive ? classes.link : classes.item}>Profile</NavLink></div>
        <div className={classes.item}><NavLink to="/dialogs"className={navData => navData.isActive ? classes.link : classes.item}>Messages</NavLink></div>
        <div className={classes.item}><NavLink to="/news" className={navData => navData.isActive ? classes.link : classes.item}>News</NavLink></div>
        <div className={classes.item}><NavLink to="/music" className={navData => navData.isActive ? classes.link : classes.item}>Music</NavLink></div>
        <div className={classes.item}><NavLink to="/settings" className={navData => navData.isActive ? classes.link : classes.item}>Settings</NavLink></div>
        <div className={classes.item}><NavLink to="/users" className={navData => navData.isActive ? classes.link : classes.item}>Users</NavLink></div>
        
      </div>
    </nav>
    );
}
export default Navbar;