import {profileAPI, usersAPI} from './../api/api';
let ADD_POST_ACTION_CREATOR = "ADD_POST_ACTION_CREATOR";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_STATUS = "SET_STATUS";
const DELETE_POST = "DELETE_POST";

// ,    redax
let initialState = {
   postsData : 
   [
   {id: 1, message:"Hi, hov are you?", like: "0" },
   {id: 2, message:"Old or young?", like: "23" },
   ],
   profile: null,
   status: ''
}



//state=initialState -     , ..     
const myPostDataReducer = (state = initialState, action) => {

    switch(action.type) {
        case ADD_POST_ACTION_CREATOR:{ 
          let newObj = {
            id: 3,
            message: action.newPostText,
            like: "0"
         } 
          return{
            ...state,
            postsData: [...state.postsData, newObj],
            textareData: ""
          }


      }
        case SET_USER_PROFILE: {
         return {
            ...state,
            profile: action.profile
         }
        }
        case SET_STATUS: {
         return {
            ...state,
            status: action.status
         }
        }
           default:
           return state; 
        
    }

}

//Action Creator
export const addPostActionCreator = (newPostText) =>  ({type: ADD_POST_ACTION_CREATOR, newPostText});
const setUserProfile = (profile) => ({type: SET_USER_PROFILE, profile});
const setStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (postId) => ({type: DELETE_POST, postId});/// -?   

export const getUserProfile = (userId) => async (dispatch) => {
   let response = await usersAPI.getProfile(userId);
      dispatch(setUserProfile(response.data));
}

export const getStatus = (userId) => async (dispatch) => {
   let response = await profileAPI.getStatus(userId);
      dispatch(setStatus(response.data))
}

export const updateStatus = (status) => async (dispatch) => {
   let response = await profileAPI.updateStatus(status);
      if(response.data.resultCode === 0) {
         dispatch(setStatus(status))
      }
}


export default myPostDataReducer;