//variables for Action Creator
let SEND_MESSADGE = "SEND_MESSADGE";


let initialState =     {
        
    messageData : 
    [
        {id: 1, message: "Hi"},
        {id: 2, message: "Hello"},
        {id: 3, message: "How are you"},
        {id: 4, message: "Thanks"},
        {id: 5, message: "Yo"}
    ],

    
    dialogesData : 
    [
    {id: 1, name: "Dima"},
    {id: 2, name: "Olya"},
    {id: 3, name: "Nika"},
    {id: 4, name: "Nikita"},
    {id: 5, name: "Ksenia"}
    ]
  
}


const dialogsReducer = (state = initialState, action) => {


     switch(action.type){

        case SEND_MESSADGE:{ 
        
            let body = action.newMessageBody;
            return{
                ...state,
                messageData: [...state.messageData, {id: 6, message: body}]
              
            }
               
        }
        default: 
            return state;    


     }

}


//Action Creator
export const sendMessageCreator = (newMessageBody) => ({type: SEND_MESSADGE, newMessageBody});

export default dialogsReducer;