import "./App.css";
import React, { Suspense } from "react";
import HeaderContainer from "./components/Header/HeaderContainer";
import Navbar from "./components/Navbar/navbar";
import News from "./components/News/News";
import {  Routes, Route } from "react-router-dom";
// import DialogesContainer from "./components/Dialoges/DialogesContainer";
// import UsersContainer from "./components/Users/UsersContainer";
// import ProfileContainer from "./components/Profile/ProfileContainer";
import Login from "./components/Login/Login";
import { connect } from "react-redux";
import { compose } from "redux";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { initializeApp } from "./redux/app-reducer";
import { Preloader } from "./common-components/Preloader/Preloader";

const DialogesContainer = React.lazy(() => import("./components/Dialoges/DialogesContainer"));
const  UsersContainer = React.lazy(() => import("./components/Users/UsersContainer"));
const  ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer"));

class App extends React.Component {

  componentDidMount() {
    this.props.initializeApp();
     }

  render() {

    if(!this.props.initialized) {
      return <Preloader />
    }
    return (
     
        <div className="app-wrapper">
          <Navbar />
          <HeaderContainer />
        <div className="app-wrapper-content">

            <Suspense fullback={<Preloader />}>
            <Routes>
              <Route path="/dialogs/*" element={<DialogesContainer />} />
              <Route path=":userId" element={<ProfileContainer />} />
              <Route path="/profile/*" element={<ProfileContainer />} />
              <Route path="/news/*" element={<News />} />
              <Route path="/users/*" element={<UsersContainer />} />
              <Route path="/login/*" element={<Login />} />
            </Routes>
            </Suspense>
          </div>
        </div>
   
    );
  }
}

function withRouter(Component) {
  function ComponentWithRouterProp(props) {
     let location = useLocation();
     let navigate = useNavigate();
     let params = useParams();
     return (
      <Component {...props} router={{location, navigate, params}} />
     );
  }
  return ComponentWithRouterProp;
}


const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})

export default compose( 
  withRouter,
connect(mapStateToProps, {initializeApp})
)(App)


