import {usersAPI}  from '../../src/api/api';
import { updateObjectInArray } from '../utils/validators/object-helpers';
const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const SET_CURRENT_PAGE = "SET_CURRENT_PAGE";
const SET_TOTAL_USERS_COUNT = "SET_TOTAL_USERS_COUNT";
const SET_TOGGLE_IS_FETCHING = "SET_TOGGLE_IS_FETCHING ";
const SET_TOGGLE_IS_FOLLOWING_PROGRESS = "SET_TOGGLE_IS_FOLLOWING_PROGRESS ";


let initialState = {
  users: [],
  pagesSize: 800,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: true,
  followingInProgress: [] // 
};

export const usersReducer = (state = initialState, action) => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {followed: true})
      };

    case UNFOLLOW:
      return {
        ...state,
        users: updateObjectInArray(state.users, action.userId, "id", {followed: false})
      };

    //  
    //  ,     ,    
    case SET_USERS:
      return {
        ...state,
        users: action.users,
      };

    case SET_CURRENT_PAGE:
      return {
        ...state,
        currentPage: action.currentPage,
      };

    case SET_TOTAL_USERS_COUNT:
      return {
        ...state,
        totalUsersCount: action.count,
      };

      case SET_TOGGLE_IS_FETCHING:
        return{
          ...state,
        isFetching: action.isFetching
        };

      case SET_TOGGLE_IS_FOLLOWING_PROGRESS : // 
        return{
          ...state,
        followingInProgress: action.isFetching 
        ? [...state.followingInProgress, action.userId] 
        : state.followingInProgress.filter(id => id !== action.userId)
        };

    default:
      return state;
  }
};

//AC -    ActionCreator
export const followSuccess = (userId) => ({ type: FOLLOW, userId });
export const unfollowSuccess = (userId) => ({ type: UNFOLLOW, userId });

//    
const setUsers = (users) => ({ type: SET_USERS, users });
const setCurrentPage = (currentPage) => ({type: SET_CURRENT_PAGE, currentPage: currentPage});
const setTotalUsersCount = (totalUsersCount) => ({type: SET_TOTAL_USERS_COUNT, count: totalUsersCount});
const setIsFetching = (isFetching) => ({type: SET_TOGGLE_IS_FETCHING, isFetching: isFetching});
// 
export const toggleFollowingProgress = (isFetching, userId) => ({type: SET_TOGGLE_IS_FOLLOWING_PROGRESS , isFetching, userId});


//thunk
export const getUsers = (currentPage, pageSize) => {
 return async (dispatch) => {
  dispatch(setIsFetching(true)); 
  let data = await usersAPI.getUsers(currentPage, pageSize);
      dispatch(setCurrentPage(currentPage));
      dispatch(setIsFetching(false));
      dispatch(setUsers(data.items));
      dispatch(setTotalUsersCount(data.totalCount));
}
}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
  dispatch(toggleFollowingProgress(true, userId));
  let response = await apiMethod(userId);
        if (response.data.resultCode === 0) {
          dispatch(actionCreator(userId));
         } 
         dispatch(toggleFollowingProgress(false, userId));
}

export const follow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(userId), followSuccess);
 }
 }

 export const unfollow = (userId) => {
  return async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(userId), unfollowSuccess);
 }
 }
